body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.main-comp {
  width: 96%;
  margin-left: 2%;
  margin-top: 2%;
}

.filter-form {
  margin-bottom: 1%;
  margin-top: 1%;
}

.logo {
  width: 118px;
  height: 54px;
}

.filter-btn {
  height: 100%;
}

.movie {
  padding: 5px 25px 10px 25px;
  width: 15%;
  text-align: center;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.movie-image {
  min-height: 240px;
}

.more-info-content {
  margin-top: 10%;
  margin-left: 10%;
  display: inline-flex;
  width: 70%;
}

.details {
  margin-left: 2%;
}
